body {
  font-family: Hiragino Kaku Gothic ProN;
}

header {
  position: fixed;
  width: 100%;
  z-index: 100;
  background: white;
  box-shadow: 0px 3px 6px #00000029;
}

.top-background_parallax {
  background: url("https://todokede.sakura.ne.jp/ne2/wp-content/uploads/2021/12/com-fv-bg-1.jpg");
  width: 100%;
  height: 80vh;
  background-repeat: no-repeat;
  position: fixed;
  top: 0;
  background-size: cover;
  z-index: -5;
}

.l-header .header-menu nav ul li a {
  color: var(--unnamed-color-333333);
  font-size: 13px;
}

.l-header .header-contact .tel .inner p .fa-phone-alt {
  font-size: 20px;
  color: #01588E;
}

.top-banner {
  height: 570px;
  padding-top: 80px;
}

.top-banner_content {
  width: 1200px;
  margin: auto;
  text-align: center;
  padding: 70px 0;
}

@media screen and (max-width: 767px) {
  .top-banner_content {
    width: 100%;
  }
  .top-banner_content img {
    width: 100%;
  }
}

.top-banner_content .block-content_1 {
  display: flex;
  justify-content: center;
  align-items: center;
}

.top-banner_content .block-content_1 .content-left {
  display: flex;
  margin-right: 35px;
}

.top-banner_content .block-content_1 .content-left p {
  font-size: 25px;
}

.top-banner_content .block-content_1 .content-right {
  border: 1px solid white;
  border-radius: 52px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px 16px;
}

.top-banner_content .block-content_1 .content-right p {
  font-size: 18px;
  color: white;
}

.top-banner_content .block-content_2 {
  margin: 48px 0 50px;
}

.top-banner_content .block-content_2 h2 {
  font-size: 36px;
  color: white;
  margin-bottom: 40px;
}

.top-banner_content .block-content_2 h1 {
  font-size: 60px;
  color: white;
}

.top-banner_content .block-content_2 h1 small {
  font-size: 42px;
}

.top-banner_content .block-content_3 p {
  color: white;
  font-size: 18px;
  margin-bottom: 10px;
}

.front-top {
  background: white;
  padding: 95px 0;
}

.front-top .top {
  margin: auto;
}

.top h3 {
  background-color: #01588E;
  position: relative;
}

.top h3::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  border-top: 0 solid #ffffff;
  border-left: 0 solid #f5d97b6e;
  border-bottom: 0 solid #D94948;
  border-right: 0 solid #8db43400;
  border-top-width: 42px;
  border-left-width: 0px;
  border-right-width: 18px;
  border-bottom-width: 0px;
}

.top h3::after {
  position: absolute;
  content: "";
  top: 0;
  right: 0;
  border-top: 0 solid #ffffff00;
  border-left: 0 solid #ffffff00;
  border-bottom: 0 solid #ffffff;
  border-right: 0 solid #ffffff;
  border-top-width: 42px;
  border-left-width: 0px;
  border-right-width: 18px;
  border-bottom-width: 0px;
}

.top p.blue-bold {
  font-size: 18px;
}

.top .price-block {
  color: #333333;
  font-weight: 600;
  width: auto;
}

.top .top-text {
  width: auto;
  color: #333333;
}

.top .bg-gray {
  width: auto;
  color: #333333;
}

.apply-block {
  padding: 40px 0 36px;
}

.apply-block .apply-block-inner .free-dial .left {
  padding: 10px 20px 10px 10px;
}

.apply-block .apply-block-inner .free-dial .left p.bigger {
  margin-bottom: 10px;
}

.apply-block .apply-block-inner .free-dial .right {
  text-align: center;
  padding: 10px 0 10px 20px;
}

.apply-block .apply-block-inner .free-dial .right p.bigger {
  margin-bottom: 15px;
}

.apply-block-inner .block-content_1 {
  display: flex;
  justify-content: center;
  align-items: center;
}

.apply-block-inner .block-content_1 .slash {
  font-size: 24px;
  margin: 0 6px;
}

.top-reason {
  background: white;
}

.top-reason .reason-block .reason-box .item-num {
  top: -50px;
}

.top-reason .reason-block .reason-box .item-num h3 {
  font-family: Roboto;
}

.questionnaire h3 .bigger {
  font-size: 40px;
}

.questionnaire h3 .slash {
  margin: 0 15px;
  font-weight: 300;
  font-size: 45px;
}

.questionnaire .customer-answer {
  width: auto;
  max-width: 1200px;
  margin: auto;
}

.questionnaire .customer-answer .voice {
  padding: 45px 38px;
  min-width: 220px;
  width: auto;
  position: relative;
}

.questionnaire .customer-answer .voice p {
  line-height: 42px;
}

.questionnaire .customer-answer .voice:after {
  position: absolute;
  content: "";
  left: 43%;
  bottom: -35px;
  border-top: 0 solid #ffffff;
  border-left: 0 solid #f5d97b6e;
  border-bottom: 0 solid #D94948;
  border-right: 0 solid #8db43400;
  border-top-width: 35px;
  border-left-width: 0px;
  border-right-width: 35px;
  border-bottom-width: 0px;
}

.result-title {
  padding: 70px 0 15px 0;
  text-align: center;
  width: 1200px;
  margin: auto;
  position: relative;
}

@media screen and (max-width: 767px) {
  .result-title {
    width: 90%;
  }
}

.result-title .text-center {
  font-size: 24px;
  display: inline-block;
  margin: 0;
}

.result-title .text-right {
  position: absolute;
  right: 0;
  font-size: 20px;
}

.result {
  max-width: 1000px;
  margin: auto;
  background: #FFFFFF 0% 0% no-repeat padding-box;
  padding: 30px 100px 25px;
  display: flex;
  justify-content: space-between;
}

@media screen and (max-width: 767px) {
  .result {
    width: 100%;
    display: block;
    max-width: none;
    padding: 0;
  }
}

.result-nd_title h4 {
  color: #333333;
}

.result-nd_title h4 small {
  font-size: 13px;
}

.result-content {
  margin-top: 25px;
}

.result-content .ranking {
  color: #333333;
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.ranking .ranking-number {
  background: transparent;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #333333;
  margin-right: 15px;
}

.ranking .ranking-number.rank-gold {
  background-color: #D9AA1E;
}

.ranking .ranking-number.rank-silver {
  background-color: #C7D1D9;
}

.ranking .ranking-number.rank-bronze {
  background-color: #BF6E26;
}

.btn {
  position: relative;
}

.btn .fa-chevron-right {
  border-radius: 50%;
  border: 1px solid white;
  width: 24px;
  height: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 10px;
  top: 10px;
  color: white;
}

.top-customer-voice {
  background-color: white;
  padding: 100px 0;
}

.top-customer-voice .wrapper {
  display: flex;
  flex-wrap: wrap;
}

.top-customer-voice .wrapper .voice-box {
  flex-grow: 1;
  width: 33%;
  text-align: center;
}

@media screen and (max-width: 767px) {
  .top-customer-voice {
    display: block;
  }
  .top-customer-voice .wrapper {
    display: block;
  }
  .top-customer-voice .wrapper .voice-box {
    flex-grow: 1;
    width: 70%;
    margin: 0 auto;
    text-align: center;
  }
}

.voice-box .voice-box_content {
  max-width: 220px;
  margin: auto;
  color: #333333;
  padding-bottom: 60px;
}

.voice-box .voice-box_content img {
  width: 100%;
  height: 210px;
}

.voice-box .voice-box_content .name {
  padding: 12px 5px 15px;
  font-size: 13px;
  line-height: 23px;
  border-bottom: 1px solid #6D7E8C;
  margin-bottom: 10px;
}

.voice-box .voice-box_content .text {
  text-align: left;
  line-height: 28px;
}

.top-section-flow {
  padding: 90px 0 130px;
  background-color: #F5F5F5;
}

.top-section-flow .flow-title-box {
  text-align: center;
  margin-bottom: 70px;
}

.top-flow-title-box .flow-title {
  color: #6D7E8C;
  font-size: 20px;
  margin-bottom: 50px;
  position: relative;
  text-align: center;
  font-weight: 300;
}

.top-flow-title-box .flow-title::after {
  background-color: #6D7E8C;
  bottom: -15px;
  content: "";
  height: 20px;
  left: 50%;
  position: absolute;
  transform: translate(-50%, 100%);
  width: 0.1px;
}

.top-flow-title-box .flow-title__sub {
  color: #035AA6;
  font-size: 42px;
  font-weight: bold;
  line-height: 1.714285em;
  margin-bottom: 35px;
  text-align: center;
}

.top-flow-title-box .flow-free {
  background-color: #FADD62;
  border-radius: 50%;
  width: 100px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 19px;
  color: #01588E;
  margin: auto;
}

.top-step {
  display: flex;
  flex-wrap: wrap;
}

@media screen and (max-width: 767px) {
  .top-step {
    display: block;
  }
}

.top-step .step-item {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  width: 25%;
}

.top-step .step-item .step-item__illust .step-item__illust-step {
  color: #01588E;
  font-family: Roboto;
  font-size: 20px;
  font-weight: bold;
  position: relative;
  border-bottom: 3px solid #56AEDD;
}

.top-step .step-item .step-item__illust .step-item__illust-step span {
  font-size: 42px;
  margin-left: 5px;
}

.top-step .step-item .step-item__illust .step-item__illust-step::after {
  position: absolute;
  content: "";
  width: 50px;
  height: 3px;
  background-color: #56AEDD;
  transform: rotate(45deg);
  right: -6px;
  bottom: 15px;
}

.top-step .step-item .step-item__illust .step-item__illust-icon {
  width: 100%;
  height: 165px;
  padding: 20px 10px;
  text-align: center;
}

.top-step .step-item .step-item__illust .step-item__illust-icon img {
  width: 165px;
  height: 120px;
  object-fit: cover;
}

.top-step .step-item .step-box {
  max-width: 220px;
  margin: auto;
  text-align: center;
  padding-bottom: 80px;
}

.top-step .step-item .step-box .step-item__illust {
  text-align: left;
}

.top-step .step-item .step-box .step-item__text {
  height: 55px;
}

.top-step .step-item .step-box .step-item__text h3.step-item__text-title {
  font-size: 22px;
  color: #333333;
  line-height: 28px;
}

.top-step .step-item .step-box .step-item__text small {
  font-size: 16px;
  line-height: 28px;
}

.top-flow-alert {
  max-width: 820px;
  margin: auto;
  font-size: 16px;
  line-height: 28px;
}

section.top-conform {
  max-width: 700px;
  margin: auto;
  background: white;
  padding: 50px;
  color: #333333;
  margin-top: 50px;
}

@media screen and (max-width: 767px) {
  section.top-conform {
    box-sizing: border-box;
    max-width: none;
    width: 100%;
    padding: 10px;
  }
}

section.top-conform h2.conform-title {
  display: flex;
  justify-content: center;
  font-size: 24px;
  line-height: 42px;
  margin-bottom: 60px;
}

section.top-conform p.conform-alert {
  margin-bottom: 60px;
  font-size: 16px;
  line-height: 28px;
}

section.top-conform p.conform-alert a {
  color: #56AEDD;
  text-decoration: none;
}

section.top-conform ul.conform-lists {
  max-width: 500px;
  margin: auto;
  color: #01588E;
}

section.top-conform ul.conform-lists li.conform-lists__item {
  margin-bottom: 40px;
  position: relative;
  list-style: none;
}

section.top-conform ul.conform-lists li.conform-lists__item:last-child {
  margin-bottom: 0px;
}

section.top-conform ul.conform-lists li.conform-lists__item h3.conform-lists__item__title {
  font-size: 18px;
  line-height: 32px;
  color: #333333;
}

section.top-conform ul.conform-lists li.conform-lists__item::before {
  content: "";
  position: absolute;
  top: 10px;
  left: -30px;
  width: 13px;
  height: 13px;
  background: #01588E;
}

.bg-white {
  background-color: white;
}

section.comments {
  padding: 30px 0;
}

.appeal {
  padding-bottom: 0px;
}
